import {useEffect, useState} from 'react';
import {Link, useParams} from 'react-router-dom';
import {shuffled} from '../utils';
import styles from './LearnRoute.module.scss';

export default function LearnRoute({sectionData}) {
  const {sectionCodeName} = useParams();

  const [toSolve, setToSolve] = useState(null);
  const [solved, setSolved] = useState(null);

  const [current] = toSolve ?? [];
  const [isSolution, setIsSolution] = useState(null);

  console.log(toSolve);

  // initialize component with
  useEffect(() => {
    const itemWithSectionName = (section) => (item) => {
      return {...item, sectionFriendlyName: section.friendlyName};
    };

    const setShuffledToSolve = (arr) => {
      setToSolve(shuffled(arr));
    };

    if (sectionCodeName === 'all') {
      setShuffledToSolve(sectionData.map(section => section.items.map(itemWithSectionName(section))).flat ());
    } else {
      const section = sectionData.find(section => section.codeName === sectionCodeName);
      if (section == null) {
        setToSolve(null);
        setSolved(null);
        return;
      }
      setShuffledToSolve(section.items.map(itemWithSectionName(section)));
    }
    setSolved([]);
  }, [sectionCodeName, sectionData]);

  const handleGoNext = (event, isSuccess) => {
    event.preventDefault();
    if (isSuccess) {
      setToSolve(toSolve.slice(1));
      setSolved([...solved, current]);
    } else {
      if (toSolve.length > 1) {
        setToSolve([...toSolve.slice(1, 2), current, ...toSolve.slice(2)]);
      }
    }
    setIsSolution(false);
  };
  const handleCorrect = (event) => handleGoNext(event, true);
  const handleIncorrect = (event) => handleGoNext(event, false);

  if (toSolve == null) {
    return (
        <div>
          Nie istnieje taka sekcja, wróć do <Link to="/">strony głównej</Link>
        </div>
    );
  }

  if (current == null) {
    return <div>
      Brawo, zdałxś!!!!!!!! <button
        onClick={() => window.history.go(0)}>
      Zacznij od nowa</button> bądź <Link to="/">przejdź do menu głównego</Link>, ewentualnie idź spać
    </div>;
  }

  const handleShowAnswer = (event) => {
    event.preventDefault();
    setIsSolution(true);
  };

  return (
      <div>
        <div className={styles.imageGrid}>
          <div className={styles.questImageContainer}>
            <img src={current.imageUrl}/>
          </div>
          {isSolution ?
              <div className={styles.answerImageContainer}>
                {current.names.join(' | ')}
              </div> :
              ''}
        </div>
        <div>
          {isSolution ?
              <>
                <button onClick={handleCorrect}>Wiem</button>
                <button onClick={handleIncorrect}>Nie wiem</button>
              </> :
              <>
                <button onClick={handleShowAnswer}>Pokaż odpowiedź</button>
              </>
          }

          {/*<input type="text" value={inputValue} onChange={handleInputChanged} className={styles.input}/>*/}
        </div>
        <div>
          wiesz: {solved.length} | nie wiesz: {toSolve.length}
        </div>
      </div>
  );
}
